"use client"

import { useState, useEffect } from "react"
import { useParams, useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { 
  ChevronLeft, Loader2, Sparkles, TrendingUp, 
  Target, AlertCircle, Lightbulb, ArrowRight, Download, Share2
} from "lucide-react"

export default function ReportDetailPage() {
  const params = useParams()
  const router = useRouter()
  const [loading, setLoading] = useState(true)
  const [report, setReport] = useState<any>(null)

  useEffect(() => {
    const fetchReportDetail = async () => {
      const token = localStorage.getItem("accessToken")
      try {
        // 리포트 상세 조회 API (경로: /v1/usergoal/report/:id)
        const response = await fetch(`http://34.50.7.8:3000/v1/usergoal/report/${params.id}`, {
          method: "GET",
          headers: { "Authorization": `Bearer ${token}` }
        })
        const data = await response.json()
        if (response.ok && data.isSuccess) {
          setReport(data.result)
        } else {
          alert("리포트를 불러올 수 없습니다.")
          router.back()
        }
      } catch (e) {
        console.error(e)
      } finally {
        setLoading(false)
      }
    }

    if (params.id) fetchReportDetail()
  }, [params.id, router])

  if (loading) return (
    <div className="min-h-screen bg-background flex flex-col items-center justify-center gap-4">
      <Loader2 className="h-10 w-10 animate-spin text-primary" />
      <p className="font-black animate-pulse">AI가 정밀 분석 리포트를 구성 중입니다...</p>
    </div>
  )

  return (
    <div className="min-h-screen bg-background text-foreground pb-20">
      {/* Header */}
      <header className="border-b bg-card h-16 flex items-center px-4 sticky top-0 z-50 justify-between">
        <div className="flex items-center">
          <Button variant="ghost" size="icon" onClick={() => router.back()}>
            <ChevronLeft className="h-5 w-5" />
          </Button>
          <h1 className="ml-2 text-sm font-black uppercase tracking-widest italic">Analysis Report</h1>
        </div>
        <div className="flex gap-2">
          <Button variant="ghost" size="icon"><Share2 className="h-4 w-4" /></Button>
          <Button variant="ghost" size="icon"><Download className="h-4 w-4" /></Button>
        </div>
      </header>

      <main className="container mx-auto max-w-2xl px-4 py-8 space-y-8">
        
        {/* Title Section */}
        <section className="space-y-2">
          <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-primary/10 text-primary text-[10px] font-black uppercase tracking-wider">
            <Sparkles className="h-3 w-3" /> AI Personalized Strategy
          </div>
          <h2 className="text-3xl font-black leading-tight">
            {report?.goalTitle || "맞춤형 재무 분석 결과"}
          </h2>
          <p className="text-muted-foreground text-sm font-medium">
            분석 일시: {new Date(report?.createdAt).toLocaleString()}
          </p>
        </section>

        {/* AI Executive Summary */}
        <Card className="p-6 border-none bg-primary text-white shadow-xl shadow-primary/20 relative overflow-hidden">
          <div className="relative z-10 space-y-4">
            <div className="flex items-center gap-2">
              <Lightbulb className="h-6 w-6 text-yellow-300" />
              <h3 className="text-xl font-black">AI 총평</h3>
            </div>
            <p className="leading-relaxed font-bold opacity-95 text-lg">
              "{report?.summary || "현재 자산 구조를 바탕으로 공격적인 포트폴리오를 구성할 시 5년 내 목표 달성 확률이 85%로 매우 높습니다."}"
            </p>
          </div>
          <div className="absolute top-[-20%] right-[-10%] opacity-10">
            <Target size={200} />
          </div>
        </Card>

        {/* Detailed Metrics */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <Card className="p-5 bg-card border-none shadow-md space-y-3">
            <div className="flex items-center gap-2 text-primary">
              <TrendingUp className="h-4 w-4" />
              <span className="text-xs font-black uppercase tracking-widest">예상 수익률</span>
            </div>
            <p className="text-2xl font-black">{report?.expectedReturn || "연 8.5% ~ 12.0%"}</p>
          </Card>
          <Card className="p-5 bg-card border-none shadow-md space-y-3">
            <div className="flex items-center gap-2 text-orange-500">
              <AlertCircle className="h-4 w-4" />
              <span className="text-xs font-black uppercase tracking-widest">리스크 등급</span>
            </div>
            <p className="text-2xl font-black text-orange-500">{report?.riskLevel || "다소 높음"}</p>
          </Card>
        </div>

        {/* Solution Roadmap */}
        <section className="space-y-4">
          <h3 className="text-lg font-black flex items-center gap-2">
            <ArrowRight className="h-5 w-5 text-primary" /> 단계별 실행 로드맵
          </h3>
          <div className="space-y-4 border-l-2 border-primary/20 ml-2 pl-6">
            {report?.roadmap ? report.roadmap.map((step: any, idx: number) => (
              <div key={idx} className="relative">
                <div className="absolute -left-[31px] top-1 h-3 w-3 rounded-full bg-primary shadow-[0_0_10px_rgba(var(--primary),0.5)]" />
                <div className="space-y-1">
                  <p className="text-xs font-black text-primary">{step.period || `Step ${idx + 1}`}</p>
                  <p className="font-black text-lg">{step.title}</p>
                  <p className="text-sm text-muted-foreground font-medium">{step.description}</p>
                </div>
              </div>
            )) : (
              <p className="text-muted-foreground">상세 로드맵 데이터가 없습니다.</p>
            )}
          </div>
        </section>

        {/* Recommendations */}
        <section className="space-y-4">
          <h3 className="text-lg font-black">추천 투자 상품</h3>
          <div className="grid gap-3">
            {report?.recommendations?.map((item: any, idx: number) => (
              <Card key={idx} className="p-4 bg-secondary/20 border-none flex justify-between items-center group cursor-pointer hover:bg-secondary/40 transition-colors">
                <div className="flex items-center gap-4">
                  <div className="h-10 w-10 rounded-xl bg-background flex items-center justify-center font-black text-primary">
                    {idx + 1}
                  </div>
                  <div>
                    <p className="font-black text-sm">{item.name}</p>
                    <p className="text-[10px] text-muted-foreground font-bold">{item.category}</p>
                  </div>
                </div>
                <Button variant="ghost" size="icon" className="group-hover:translate-x-1 transition-transform">
                  <ChevronLeft className="h-4 w-4 rotate-180" />
                </Button>
              </Card>
            ))}
          </div>
        </section>

        <Button 
          className="w-full h-14 font-black text-lg mt-8"
          onClick={() => router.push("/dashboard")}
        >
          대시보드로 돌아가기
        </Button>
      </main>
    </div>
  )
}